<template>
    <v-fab-transition>
        <v-btn
        v-show="!hidden"
        color="pink"
        fab
        dark
        large
        fixed
        bottom
        right
        class="mb-5"
        @click="onClick"
        >
        <v-icon>{{icon}}</v-icon>
        </v-btn>
    </v-fab-transition>
</template>

<script>
export default {
    data() {
        return {
            hidden: true,
            loved: false
        }
    },
    beforeMount() {
        this.hidden = false;
    },
    computed:{
        icon() {
            if(this.loved) {
                return 'fas fa-heart';
            }
            else {
                return 'far fa-heart'
            }
        }
    },
    methods: {
        onClick() {
            this.loved = !this.loved;
        }
    }
}
</script>
